import { supabase } from '@/lib/supabase'
import { UserProfile, UserProfileInsert } from '@/types/database'
import { TossLoginResult } from '@/types/toss'

export class TossAuthService {
  // í† ìŠ¤ ì •ë³´ë¡œ Supabase ì‚¬ìš©ì ìƒì„±/ë¡œê·¸ì¸
  static async createOrUpdateUser(tossData: TossLoginResult) {
    const { user, token, auth } = tossData
    
    // í† ìŠ¤ userKeyë¥¼ ì´ë©”ì¼ë¡œ ë³€í™˜ (ê³ ìœ  ì‹ë³„ì)
    const email = `${user.userKey}@toss.health-hero.app`
    const password = `toss_${user.userKey}_${Date.now()}`

    try {
      // 1. ê¸°ì¡´ ì‚¬ìš©ì í™•ì¸
      const existingProfile = await this.findUserByTossKey(user.userKey)
      
      let userId: string

      if (existingProfile) {
        // ê¸°ì¡´ ì‚¬ìš©ì - í† ìŠ¤ ì •ë³´ë¡œ Supabase ë¡œê·¸ì¸
        const { data: signInData, error: signInError } = await supabase.auth.signInWithPassword({
          email: existingProfile.email || email,
          password: `toss_${user.userKey}_permanent`
        })

        if (signInError) {
          // ë¹„ë°€ë²ˆí˜¸ê°€ í‹€ë¦° ê²½ìš°, ì‚¬ìš©ìë¥¼ ì°¾ì•„ì„œ ì„¸ì…˜ ìƒì„±
          console.log('ê¸°ì¡´ ì‚¬ìš©ì ì¬ë¡œê·¸ì¸ ì‹œë„...')
          throw signInError
        }

        userId = signInData.user.id
      } else {
        // ì‹ ê·œ ì‚¬ìš©ì - Supabase ê³„ì • ìƒì„±
        const { data: signUpData, error: signUpError } = await supabase.auth.signUp({
          email,
          password: `toss_${user.userKey}_permanent`, // ê³ ì •ëœ ë¹„ë°€ë²ˆí˜¸ ì‚¬ìš©
          options: {
            data: {
              toss_user_key: user.userKey,
              name: user.name
            }
          }
        })

        if (signUpError) {
          throw signUpError
        }

        if (!signUpData.user) {
          throw new Error('ì‚¬ìš©ì ìƒì„±ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.')
        }

        userId = signUpData.user.id
      }

      // 2. ì‚¬ìš©ì í”„ë¡œí•„ ì—…ë°ì´íŠ¸/ìƒì„±
      const tokenExpiresAt = new Date(Date.now() + token.expiresIn * 1000).toISOString()
      
      const profileData: UserProfileInsert = {
        id: userId,
        email,
        name: user.name,
        toss_user_key: user.userKey,
        toss_access_token: token.accessToken,
        toss_refresh_token: token.refreshToken,
        toss_token_expires_at: tokenExpiresAt,
        toss_referrer: auth.referrer,
        level: existingProfile?.level || 1,
        current_exp: existingProfile?.current_exp || 0,
        total_score: existingProfile?.total_score || 0,
        current_streak: existingProfile?.current_streak || 0,
        current_stage: existingProfile?.current_stage || 1,
        current_phase: existingProfile?.current_phase || 1
      }

      const { data: profile, error: profileError } = await supabase
        .from('user_profiles')
        .upsert(profileData, {
          onConflict: 'id'
        })
        .select()
        .single()

      if (profileError) {
        console.error('í”„ë¡œí•„ ì €ì¥ ì‹¤íŒ¨:', profileError)
        throw profileError
      }

      // 3. ë¡œê·¸ì¸ ê¸°ë¡ ì €ì¥
      await supabase
        .from('toss_login_logs')
        .insert({
          user_id: userId,
          toss_user_key: user.userKey,
          referrer: auth.referrer,
          user_agent: typeof navigator !== 'undefined' ? navigator.userAgent : null
        })

      return {
        userId,
        profile
      }
    } catch (error) {
      console.error('Supabase ì‚¬ìš©ì ìƒì„±/ì—…ë°ì´íŠ¸ ì‹¤íŒ¨:', error)
      throw error
    }
  }

  // í† ìŠ¤ userKeyë¡œ ê¸°ì¡´ ì‚¬ìš©ì ì°¾ê¸°
  static async findUserByTossKey(tossUserKey: number): Promise<UserProfile | null> {
    try {
      const { data, error } = await supabase
        .from('user_profiles')
        .select('*')
        .eq('toss_user_key', tossUserKey)
        .single()

      if (error) {
        if (error.code === 'PGRST116') {
          // ì‚¬ìš©ì ì—†ìŒ
          return null
        }
        throw error
      }

      return data
    } catch (error) {
      console.error('ì‚¬ìš©ì ì¡°íšŒ ì‹¤íŒ¨:', error)
      return null
    }
  }

  // í˜„ì¬ ë¡œê·¸ì¸ëœ ì‚¬ìš©ì í”„ë¡œí•„ ê°€ì ¸ì˜¤ê¸°
  static async getCurrentUserProfile(): Promise<UserProfile | null> {
    try {
      const { data: { user } } = await supabase.auth.getUser()
      
      if (!user) {
        return null
      }

      const { data, error } = await supabase
        .from('user_profiles')
        .select('*')
        .eq('id', user.id)
        .single()

      if (error) {
        throw error
      }

      return data
    } catch (error) {
      console.error('í˜„ì¬ ì‚¬ìš©ì í”„ë¡œí•„ ì¡°íšŒ ì‹¤íŒ¨:', error)
      return null
    }
  }

  // ì‚¬ìš©ì í”„ë¡œí•„ ì—…ë°ì´íŠ¸
  static async updateUserProfile(userId: string, updates: Partial<UserProfile>) {
    try {
      const { data, error } = await supabase
        .from('user_profiles')
        .update(updates)
        .eq('id', userId)
        .select()
        .single()

      if (error) {
        throw error
      }

      return data
    } catch (error) {
      console.error('í”„ë¡œí•„ ì—…ë°ì´íŠ¸ ì‹¤íŒ¨:', error)
      throw error
    }
  }
}

